import React, { Component } from 'react'
import PropTypes from 'prop-types'

const logo = 'data:image/png;base64,'

class AboutModal extends Component {
  static propTypes = {
    options: PropTypes.object.isRequired
  }

  render () {
    const { extensions } = this.props.options
    const { version, jsreportVersion } = extensions.jo.options

    return (
      <div>
        <h2>About</h2>
        <div>
          <img src={logo} style={{ width: '100px', height: 'auto', marginBottom: '15px' }} />
        </div>
        <div>
          jsreportonline version: <b>{version}</b>
        </div>
        <div>
          jsreport version: <b>{jsreportVersion}</b>
        </div>
        <br />
        <br />
        <div>
          See more information about the jsreportonline release process and also release notes
        </div>
        <br />
        <div>
          <a
            className='button confirmation'
            href='https://jsreport.net/learn/online-versions'
            target='_blank'
            style={{ marginLeft: 0 }}
            rel='noreferrer'
          >
            jsreportonline releases
          </a>
        </div>
      </div>
    )
  }
}

export default AboutModal
